import React, {Component} from 'react';
import Header from '../components/Header';
import Container from '../components/Container';
import FormElement from '../components/FormElement';
import PhoneNumber from '../components/PhoneNumber';
import {Row, Button} from 'react-bootstrap';
import {getIndustrySegments, getBusinessSegments} from '../services/MasterData';
import {registerManufacturer} from '../services/registration';
import {FormGroup,ControlLabel,FormControl} from 'react-bootstrap';

class Register extends Component {

  constructor(props) {
    super(props);
    this.state = {};
    this.inputs = {};
    this.initState();
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  async initState() {
    const industrySegments = await getIndustrySegments();
    const businessSegments = await getBusinessSegments();
    this.setState({
      'industrySegments': industrySegments,
      'businessSegments': businessSegments
    });
  }

  handleInputChange(evt) {
    this.inputs = this.inputs || {};
    const { name, value, type, checked } = evt.target;
    const check_type = ['radio', 'checkbox'].includes(type);
    let newVal = value;
    if (type === 'number')
      newVal = parseInt(value);
    this.inputs[name] = check_type? checked : newVal;
  }

  async handleSubmit() {
    registerManufacturer(this.inputs);
  }

  render() {
    const industrySegments = this.state.industrySegments && Object.keys(this.state.industrySegments).map((opt,index) =>
      <option key={this.state.industrySegments[opt].id} value={this.state.industrySegments[opt].id}>{this.state.industrySegments[opt].name}</option>
    );
    const businessSegments = this.state.businessSegments && Object.keys(this.state.businessSegments).map((opt,index) =>
      <option key={this.state.businessSegments[opt].id} value={this.state.businessSegments[opt].id}>{this.state.businessSegments[opt].name}</option>
    );
    return(
      <div>
        <Header />
        <Container pageHeader="Manufacturer Registration">
          <form>
          <Row>
          <FormElement id="userId" labelText="User ID" type="text" name="userId" onChange={this.handleInputChange}/>
          <FormElement id="password" labelText="password" type="password" name="password" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="orgName" labelText="Name of the organization" type="text" name="orgName" onChange={this.handleInputChange}/>
          <FormElement id="gstNo" labelText="GST No" type="text" name="gstNo" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="address" labelText="Address" type="text" name="address" onChange={this.handleInputChange} />
          <div className="col-md-6">&nbsp;</div>
          </Row>
          <Row>
          <FormElement id="city" labelText="City/Town" type="text" name="city" onChange={this.handleInputChange}/>
          <FormElement id="pincode" labelText="Pincode" type="text" name="pincode" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="state" labelText="State" type="text" name="state" onChange={this.handleInputChange}/>
          <PhoneNumber onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="faxNumber" labelText="Fax No" type="number" name="faxNumber" onChange={this.handleInputChange}/>
          <FormElement id="website" labelText="Website" type="text" name="website" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormGroup className="col-md-6">
            <ControlLabel>Industry Segment</ControlLabel>
            <FormControl componentClass="select" id="industrySegment" name="industrySegment" onChange={this.handleInputChange}>
                <option key='0' value=""> --select one--</option>
                {industrySegments}
            </FormControl>
          </FormGroup>
          <FormGroup className="col-md-6">
            <ControlLabel>Business Segment</ControlLabel>
            <FormControl componentClass="select" id="businessSegment" name="businessSegment" onChange={this.handleInputChange}>
                <option key='0' value=""> --select one--</option>
                {businessSegments}
            </FormControl>
          </FormGroup>
          </Row>
          <Row>
          <FormElement id="contactPerson" labelText="Contact Person" type="text" name="contactPerson" onChange={this.handleInputChange}/>
          <FormElement id="designation" labelText="Designation" type="text" name="designation" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="department" labelText="Department" type="text" name="department" onChange={this.handleInputChange}/>
          </Row>
          <Row>
          <FormElement id="mobileNumber" labelText="Mobile No." type="number" name="mobileNumber" onChange={this.handleInputChange}/>
          <FormElement id="emailId" labelText="E-Mail ID" type="text" name="emailId" onChange={this.handleInputChange}/>
          </Row>
          <Button bsStyle="info" onClick={this.handleSubmit} >Register</Button>
          </form>
        </Container>

      </div>
    );

  }
}

export default Register;



